#headspace {
    display: flex;
    justify-content: space-between;
    padding-bottom: 65px;
    margin-top: 5.75%;
    padding-right:10%;
    align-content: center;
    height: 100px;
    width: 90%;
    margin-right:5%;
    align-items: center;
    z-index: 1;
}
#Logo {
    height: 125px;
    border-radius: .25em;
    margin: 25% 0 0 32.5%;
}
@media only screen and (max-width: 720px) {
    #Logo {
        display: none;
    }
}
@media only screen and (max-width: 350px) {
    #headspace {
        flex-direction: column;
        margin-top: 20%;
        margin-right: 0%;
    }
}